
/**
 * @param {?} value
 * @param {?} toolbar
 * @return {?}
 */
export function canEnableToolbarOptions(value: string, toolbar: any): boolean {

    if (value) {

        if (toolbar['length'] === 0) {
            return true;
        } else {

            const /** @type {?} */ found = toolbar.filter(array => {
                return array.indexOf(value) !== -1;
            });

            return found.length ? true : false;
        }
    } else {
        return false;
    }
}
/**
 * @param {?} value
 * @param {?} ngxEditorConfig
 * @param {?} input
 * @return {?}
 */
export function getEditorConfiguration(value, ngxEditorConfig, input): any {

    for (const /** @type {?} */ i in ngxEditorConfig) {
        if (i) {
            if (input[i]) {
                value[i] = input[i];
            }
            if (!value.hasOwnProperty(i)) {
                value[i] = ngxEditorConfig[i];
            }
        }
    }
    return value;
}
/**
 * @param {?} resizer
 * @return {?}
 */
export function canResize(resizer: string): any {
    if (resizer === 'basic') {
        return 'vertical';
    }
    return false;
}
